.footer-section {
  background-color: #fff5f6;
}
.footer-container {
  display: block;
}
.footer__title {
  font-family: Titan One;
  font-style: normal;
  font-weight: normal;

  text-transform: uppercase;
}
.footer__titleOne {
  width: 232px;

  font-size: 18px;
  line-height: 1.16;

  color: #ffa5ba;

  @media screen and(max-width: 768px) {
    margin-bottom: 17px;
  }
}
.footer__titleTwo {
  width: 200px;
  font-size: 34px;
  line-height: 1.14;
  letter-spacing: 0.04em;

  color: #d41443;
}
.footer__socialLinks {
  width: 32px;
  height: 32px;
  border-radius: 50%;
}

.footer__socialIcon {
  background-color: #fea5bb;
  border-radius: 50%;
}
